* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: sans-serif;
}

a {
    text-decoration: none;
    color: rgb(97, 97, 97);
}

li {
    list-style: none;
}

.btns {
    padding: 10px 20px;
    margin-right: 6px;
    background-color: transparent;
    border: none;
    border-radius: 5px;
    border: 0.6px solid #ccc;
    color: #323232;
    cursor: pointer;
}
